import intersection from 'lodash/intersection'
import {
  PreparationStage,
  OrderStatus,
  PartnerCompensationStrategy
} from '../modules/orders/constants'
import { Store } from '@/modules/store/data/resources'
import { HistoryOrder as HistoryOrderInterface } from '@/modules/store/data/resources'
import { BadRatingReason } from '@/modules/store/data/resources/history-order'
export default class HistoryOrder {
  static badRatingReasons(): BadRatingReason[] {
    return [
      BadRatingReason.WRONG_OR_MISSING_PRODUCT,
      BadRatingReason.PRICING_PROBLEM,
      BadRatingReason.BAD_PRODUCT_QUALITY,
      BadRatingReason.TASTED_BAD,
      BadRatingReason.PACKAGING_ISSUES,
      BadRatingReason.WRONG_PRODUCTS,
      BadRatingReason.MISSING_PRODUCTS,
      BadRatingReason.POOR_QUALITY,
      BadRatingReason.ALLERGY_NOT_CONSIDERED,
      BadRatingReason.CHARGED_WRONG_AMOUNT,
      BadRatingReason.SPECIAL_STORE_PROMOS
    ]
  }

  static shouldPay(orderDetails: HistoryOrderInterface | null) {
    return (
      orderDetails &&
      (!orderDetails.partnerStrategy ||
        orderDetails.partnerStrategy ===
          PartnerCompensationStrategy.PAY_PRODUCTS)
    )
  }

  static paymentMethodIsCash(orderDetails: HistoryOrderInterface | null) {
    return (
      orderDetails &&
      orderDetails.paymentMethod &&
      orderDetails.paymentMethod === 'CASH'
    )
  }

  static isAccepted(orderDetails: HistoryOrderInterface | null) {
    return (
      orderDetails &&
      orderDetails.preparationStage === PreparationStage.ACCEPTED_BY_PARTNER
    )
  }

  static isFinished(orderDetails: HistoryOrderInterface | null) {
    return (
      orderDetails &&
      (orderDetails.currentStatus === OrderStatus.DELIVERED_STATUS ||
        orderDetails.currentStatus === OrderStatus.CANCELED_STATUS)
    )
  }

  static isCancelled(orderDetails: HistoryOrderInterface | null) {
    return !!(
      orderDetails &&
      orderDetails.currentStatus === OrderStatus.CANCELED_STATUS &&
      orderDetails.cancelReason
    )
  }

  static hasBadRatings(orderDetails: HistoryOrderInterface | null) {
    return (
      orderDetails &&
      orderDetails.badRatingReasons &&
      intersection(
        orderDetails.badRatingReasons,
        HistoryOrder.badRatingReasons()
      ).length > 0
    )
  }

  static hasOriginalDescription(orderDetails: HistoryOrderInterface | null) {
    return (
      !!orderDetails &&
      !!orderDetails.originalDescription &&
      orderDetails.originalDescription.length > 0
    )
  }

  static hasRefundIncident(orderDetails: HistoryOrderInterface | null) {
    if (!orderDetails) {
      return false
    }
    return !!orderDetails.refundIncident
  }

  static isMarketplace(orderDetails: HistoryOrderInterface | null) {
    return Boolean(orderDetails && orderDetails.marketplace)
  }

  name = ''
  email = ''
  id = -1
  urn = ''
  store: Store | {} = {}
}
