
import Component from 'vue-class-component'
import Vue from 'vue'
import { Prop } from 'vue-property-decorator'
import Pair from 'common/Pair.vue'
import HistoryOrder from 'models/HistoryOrder'
import { HistoryOrder as HistoryOrderInterface } from '@/modules/store/data/resources'

@Component({
  name: 'HistoryOrderDetailsIncident',
  components: {
    Pair
  }
})
export default class HistoryOrderDetailsIncident extends Vue {
  @Prop() details!: HistoryOrderInterface

  get shouldPay(): boolean {
    return !!HistoryOrder.shouldPay(this.details)
  }

  get showTotalProductsPrice(): boolean {
    return !!this.details.cancelReason && !this.shouldPay
  }

  get hasRefundIncident(): boolean {
    return !!HistoryOrder.hasRefundIncident(this.details)
  }

  get totalToPayToPartner(): number {
    if (!this.shouldPay || !this.hasRefundIncident) return 0
    const product = this.details.products
    const incident = this.details.refundIncident
    return (
      (product ? product.total : 0) - (incident ? incident.chargedToPartner : 0)
    )
  }

  get formatNegativeAmount(): string {
    return this.totalToPayToPartner >= 0 ? '' : '-'
  }

  get shouldShowTotal(): boolean {
    return this.showTotalProductsPrice || this.hasRefundIncident
  }

  formatCurrency(value: string | number): string {
    return this.$options.filters!.currency(value)
  }
}
